import React from 'react'
import checkedMessageIcon from 'assets/img/checked-message.svg'
import checkedMessageTwiceIcon from 'assets/img/checked-message-twice.svg'
import {
    Container,
    OuterWrapper,
    ProfileIcon,
    Time,
    Title,
    Message,
    Text,
    Checked
} from './styles'
import {
    Wrapper
} from 'styles'
import personIcon from 'assets/img/person.png'

const Person = ({ className="main", width, checked = false }) => {

    return (
        <Container className={className} width={width}>
            <ProfileIcon href="#">
                <img loading="lazy" src={personIcon} alt="icon" />
            </ProfileIcon>
            <OuterWrapper href="#">
                <Wrapper justify="space-between" shadow="none" padding="0px" margin="0px">
                    <Title className={className}>Кулаков Закир Кулаков Закир </Title>
                    <Time>12:34</Time>
                </Wrapper>
                <Wrapper justify="space-between" shadow="none" padding="0px" margin="0px 0px 0px auto">
                    <Message>
                        <Text checked={checked}>Добрый день, Терещенко Бронислав</Text>
                        <Checked>
                            {checked ?
                                <img loading="lazy" src={checkedMessageIcon} alt="status" />
                            :
                                <img loading="lazy" src={checkedMessageTwiceIcon} alt="status" />
                            }
                        </Checked>
                    </Message>
                </Wrapper>
            </OuterWrapper>
        </Container>
    )
}

export default Person
